    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  @font-face {
      font-family: Chango;
      src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  }

  @font-face {
    font-family: ChelseaMarket;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }

  .dark-mode {
    background-color:black;
    color: white;
  }

  .dark-mode .mode-icon {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) ;
  } 

  /**Header*/
  .cover-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    position: relative;
  }

  .cover-container .cover-img {
    width: 100%;
  }


  .mode-icon {
    position: absolute;
    cursor: pointer;
    top: 0;
    right: 0;
    margin: 0.8vw; 
    width: 6.2vw;
    height: 2.5vw;
  }


  .cover-text {
    text-indent: 7.7vw;
    position: absolute;
  }

  .cover-text h1 {
    color: #FFA500;
    font-size: 4.6vw;
    font-family: Chango;
  }

  .cover-text h2 {
    color: #F5E8A1;
    font-size: 1.9vw;
    font-family: ChelseaMarket;
  }

  /**Main container*/
  .container {
    max-width: 87.96vw;
    padding: 1.5vw 1.2vw;
    margin-left: auto;
    margin-right: auto;
  }

  /**Images Section*/
  .images-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .images-container img {
    box-shadow: 0px 2px 10px 1px rgb(211, 208, 208);
    border-radius: 20px;
    width: 19.3vw;
    height: 25vw;
    margin: 1.5vw;
  }

  /** Media query for smaller screens */
  @media (max-width: 768px) {
    .images-container img {
      width: 35vw;
      height: 60vw;
      margin: 3vw;
    }
  }

  hr {
    border-top: 0.2vw;
    border-color: #6c757d;
    margin-top: 3vw;
  }

  /**Video Section*/
  .video-section {
    margin: 3vw;
    display: flex;
    flex-wrap: wrap;
  }

  .item {
    flex: 1;
    width: 100%;
    padding: 1vw;
  }

  @media (max-width: 768px) {
    .item {
      width:100%; 
    }
  }

  /**Footer*/
  .footer, .footer p {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 7.7vw;
    background-color: #FFA500;
    color: #F5E8A1;
    font-family: Chango;
  }
